import { mapState, mapGetters, mapMutations } from 'vuex'; //引入state，getters，mutations

import anime from 'animejs/lib/anime.es.js';
import feedBack from '@/components/feedBack.vue';
export default {
  name: 'design',
  props: {},
  data: function data() {
    return {
      download_tips_dia: false,
      cur_down_path: '',
      feedBack_flag: false,
      from_path: '/',
      menu_index: 0,
      menu_lists: [{
        tag: '我的素材',
        selected: true
      }, {
        tag: '已编辑列表',
        selected: false
      }, {
        tag: '收藏列表',
        selected: false
      }, {
        tag: '消费明细',
        selected: false
      }]
    };
  },
  watch: {
    menu_index: 'menu_tag_func'
  },
  computed: _objectSpread({}, mapState(['author_id']), {
    download_path: function download_path() {
      return this.filter_design_data[this.download_tips_index].design_img;
    }
  }),
  mounted: function mounted() {
    var me = this;
    bus.$on('changeFeedBack', function (data) {
      me.feedBack_flag = data;
    });
    me.download_func();
  },
  methods: {
    prevFunc: function prevFunc() {
      var me = this; // 返回上一页
      // me.$router.go(-1)

      me.$router.push({
        path: me.from_path
      });
    },
    download_func: function download_func() {
      //点击下载按钮
      var me = this;
      bus.$on('download', function (data) {
        me.download_tips_dia = true;
        me.cur_down_path = 'http://ht.idealead.hbindex.com/uploadFiles/images/' + data;
      });
    },
    confirm_downs_func: function confirm_downs_func() {
      // 点击确定下载
      this.downs(this.cur_down_path, false);
      this.download_tips_dia = false;
    },
    menu_tag_func: function menu_tag_func() {
      var me = this;
      var index = me.menu_index,
          len = me.menu_lists[index].tag.length; // 动画

      anime({
        targets: '.menu_line',
        easing: 'spring(1, 60, 10, 0)',
        duration: 400,
        width: function width() {
          return .2 * len + 'rem';
        },
        translateX: function translateX() {
          var tar_left = [.69, 2.29, 3.89, 5.49];

          if (index == 1) {
            return tar_left[index] + (1 - 1) / 2 - .8 + 'rem';
          } else {
            return tar_left[index] + (1 - .8) / 2 - .8 + 'rem';
          }
        }
      }); // 切换路由
      // this.setRouter(index)
    },
    setRouter: function setRouter(index) {
      var me = this; // 样式判断

      me.menu_lists.forEach(function (item) {
        item.selected = false;
      });
      me.menu_lists[index].selected = true;
      me.menu_index = index; // 切换路由

      var routerArrs = ['myMaterial', 'edited', 'collect', 'consume'];
      this.$router.push(routerArrs[index]).catch(function (err) {
        err;
      });
    }
  },
  updated: function updated() {},
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      vm.from_path = from.path;
      vm.setRouter(0);
    });
  }
};